<template>
  <div class="zhuye">
    <div class="actorcontent ">
      <div class="actorItem" v-for="item in actor">
        <div class="actorTop">
          <img :src="item.autor" class="autor">
          <span class="autorname">
                <p class="aname"><span>{{item.name}}</span><span class="loction">深圳</span><span class="loction">web前端</span></p>
                <p class="atime"><span style="color: #2196f3">{{item.title}}</span> {{item.creatTime}}</p>
              </span>
          <span style="float: right"><a :href="item.href" target="_blank" v-if="item.href">github链接</a></span>
        </div>
        <div class="actorbody">
          <div v-html="item.content" class="actorcon"></div>
          <div class="imgs" v-if="item.imags.length>0">
               <span v-for="img in item.imags" class="imgitem" @click="showimgs(item.imags,n)">
                     <img  v-lazy="path+img" :key="img">
               </span>
          </div>
          <div class="bigimgs">
               <span v-for="img in item.bigimags" class="imgitem" @click="showimgs(item.bigimags,n)">
                     <img  v-lazy="path+img" :key="img">
               </span>
          </div>
        </div>

        <div class="actorfooter">
          <a v-if="item.fromhref" :href="item.fromhref" target="_blank">{{item.fromhreftxt}}</a>
          <a v-if="!item.fromhref">{{item.fromhreftxt}}</a>
        </div>
      </div>
      <pagination :page="pagemess.page" :totalpage="pagemess.totalpage" @changepage="gopage"></pagination>
      <vueImages :imgs="images" :shoupage="n" v-if="imgsshow" @closeimgs="closeimgs"></vueImages>
    </div>
  </div>
</template>


<script>
  import pagedata from '@/dataJson/projectJson'
  import photo from "@/assets/photo.png"
  import pagination from  "@/components/comom/pagination.vue"
  import vueImages from '@/components/comom/imgs.vue'
  export default {
    components:{
      pagination,
      vueImages
    },
    data () {
      return {
        pagemess:{
          totalpage:0,
          page:1
        },
        imgsshow:false,
        n:0,
        images:[],
        actor:[]
      }
    },
      mounted:function () {
        this.getdata()

      },
      methods:{
        closeimgs:function () {
          this.imgsshow = false;
        },
        showimgs:function (imgs,n) {
          let vm = this;
          this.imgsshow = true;
          this.images = imgs.map(function (item) {
            return vm.path+item;
          });
          this.n = n;
        },
        gopage:function (page) {
          this.$router.push({path:"project",query:{page:page}});
          this.actor = [];
          this.getdata()
        },
        getdata:function () {

          let page = this.$route.query.page;
          this.pagemess.totalpage = pagedata.length;
          if(!page){
            this.pagemess.page = 1;
            this.actor = pagedata[0]
          }else {
            if(page> pagedata.length || page<1){
              this.pagemess.page = 1;
              this.actor = pagedata[0]
              return
            }
            this.pagemess.page = parseInt(page);
            this.actor = pagedata[page-1];
          }
        }
      }
  }
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
  .actorItem{
    width: 100%;
    background-color: #fff;
    padding: 16px;
    box-sizing: border-box;
    border-radius: 2px;
    margin-bottom: 20px;
  }
  .autor{
    width: 60px;
    height: 60px;
    border-radius: 50%;
    border: solid 1px #eee;
    display: inline-block;
  }
  .autorname{
    display: inline-block;
    vertical-align: top;
    margin-left: 10px;
    line-height: 25px;
  }
  .autorname .aname{
    font-size:16px ;
    font-weight: 600;
  }
  .loction{
    margin-left: 15px;
    background-color: #FFB800;
    border-radius: 5px;
    font-size: 12px;
    padding:3px 5px;
    font-weight: 100;
    color: #fff;
  }
  .actorcon{
    font-size: 14px;
    text-indent: 30px;

  }
  .imgs{
    /*max-height: 360px;*/
    overflow: hidden;
    margin: 10px 20px;
  }
  .imgs .imgitem{

    position: relative;
    float: left;
    width: 33.33%;
    box-sizing: border-box;
    padding: 0 10px 20px;
    height: 370px;

  }
  .imgitem img{
    width: 100%;
    float: left;
    height: auto;
    box-sizing: border-box;
    border: 1px solid #ddd;
    border-radius: 4px;
    padding: 5px;
  }
  .bigimgs{
    overflow: hidden;
    margin: 0 20px;
  }
  .bigimgs .imgitem{
    position: relative;
    margin-top: 10px;
    width: 100%;
    box-sizing: border-box;
    padding: 0 10px 20px;
    height: 370px;
  }
  .bigimgs .imgitem img{
    margin-top: 10px;
  }
  .actorfooter{
    margin-top: 10px;
    padding-top: 16px;
    border-top: 1px solid #f1f1f1;
    line-height:30px;
    text-align: right;
  }
</style>



